        margin:0px;
        padding:0px;
        height:100%;
        overflow:hidden;
   }    */

   .inpnum {
       width: 25%;
   }

   .dhx_cal_navline {
        visibility: hidden;
        margin: 0px;
        padding: 0px;
        height: 0px;
   }

   .overlay {
        display: flex;
        align-items:center;
        justify-content: space-between;
        flex-wrap: wrap;
        /* background: black; */
        height: 5em;
        z-index: 99;
        /* display: table;
        width: 100%;
        table-layout: fixed; */
   }

   .overlay span {
        cursor: pointer;
        display: table-cell;
        text-align: center;
        /* background: rgba(255, 255, 255, 0); */
        color: black;
        /* text-transform: uppercase; */
		font-size: 1.5em;
		/* font-weight: 0; */
    }

    .overlay span.invisible {
        background: transparent;
        color: transparent;
    }
</style>